___CSS_LOADER_EXPORT___.push([module.id, "\n.server-title[data-v-64596f4e] {\r\n  display: flex;\r\n  margin: 2%;\r\n  align-items: center;\n}\n.server-title h1[data-v-64596f4e] {\r\n  margin-left: 1%;\n}\n.form-area[data-v-64596f4e] {\r\n  display: flex;\r\n  height: 75vh;\r\n  align-items: center;\n}\n.messages-block[data-v-64596f4e] {\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 40%;\r\n  margin: auto;\r\n  text-align: center;\r\n  font-size: 1.4rem;\n}\n.message[data-v-64596f4e] {\r\n  margin: 2px;\r\n  padding: 3%;\n}\n.bounce-enter-active[data-v-64596f4e] {\r\n  animation: bounce-in-data-v-64596f4e 0.5s;\n}\n.bounce-leave-active[data-v-64596f4e] {\r\n  animation: bounce-in-data-v-64596f4e 0.5s reverse;\n}\n@keyframes bounce-in-data-v-64596f4e {\n0% {\r\n    transform: scale(0);\n}\n50% {\r\n    transform: scale(1.5);\n}\n100% {\r\n    transform: scale(1);\n}\n}\r\n", "",{"version":3,"sources":["webpack://./resources/js/components/EditForm.vue"],"names":[],"mappings":";AAgKA;EACA,aAAA;EACA,UAAA;EACA,mBAAA;AACA;AACA;EACA,eAAA;AACA;AAEA;EACA,aAAA;EACA,YAAA;EACA,mBAAA;AACA;AACA;EACA,aAAA;EACA,sBAAA;EACA,UAAA;EACA,YAAA;EACA,kBAAA;EACA,iBAAA;AACA;AACA;EACA,WAAA;EACA,WAAA;AACA;AACA;EACA,yCAAA;AACA;AACA;EACA,iDAAA;AACA;AACA;AACA;IACA,mBAAA;AACA;AACA;IACA,qBAAA;AACA;AACA;IACA,mBAAA;AACA;AACA","sourcesContent":["<template>\r\n  <div class=\"content\">\r\n    <transition-group name=\"bounce\" class=\"messages-block\">\r\n      <div :class=\"'message ' + item.class\" v-for=\"item in code\" :key=\"item.id\">\r\n        <span>\r\n          {{ item.message }}\r\n        </span>\r\n      </div>\r\n    </transition-group>\r\n    <div class=\"form-area\">\r\n      <form :action=\"formInfo.rote\" method=\"post\" @submit.prevent=\"send\">\r\n        <div class=\"w-75 m-auto form-group row\">\r\n          <label class=\"col-sm-4 col-form-label\" for=\"invite\">\r\n            Convite para o servidor(Tem que ser permanente)\r\n          </label>\r\n          <div class=\"col-sm-8\">\r\n            <input\r\n              class=\"form-control\"\r\n              type=\"text\"\r\n              id=\"invite\"\r\n              name=\"invite\"\r\n              v-model=\"formInfo.server.invite\"\r\n              placeholder=\"https://discord.gg/abcdfgh\"\r\n            />\r\n          </div>\r\n          <input type=\"hidden\" name=\"csrf\" :value=\"formInfo.csrf\" />\r\n          <label class=\"col-sm-4 col-form-label\" for=\"description\"\r\n            >uma breve descrição sobre seu servidor</label\r\n          >\r\n          <div class=\"input-group col-sm-8\">\r\n            <input\r\n              class=\"form-control\"\r\n              type=\"text\"\r\n              id=\"description\"\r\n              v-model=\"formInfo.server.description\"\r\n              name=\"description\"\r\n              placeholder=\"Um servidor amigável\"\r\n            />\r\n            <div class=\"input-group-prepend\">\r\n              <div\r\n                class=\"input-group-text\"\r\n                v-bind:class=\"{ 'text-danger': descTooLong }\"\r\n              >\r\n                {{ counter }}\r\n              </div>\r\n            </div>\r\n          </div>\r\n          <div class=\"col-sm m-3\">\r\n            <input class=\"btn btn-primary\" type=\"submit\" value=\"Editar\" />\r\n          </div>\r\n        </div>\r\n      </form>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n  name: \"Edit\",\r\n  props: {\r\n    content: String,\r\n  },\r\n  data() {\r\n    return {\r\n      formInfo: {\r\n        server: {\r\n          invite: \"\",\r\n          description: \"\",\r\n          tags: [],\r\n        },\r\n        allTags: [],\r\n        rote: \"\",\r\n        csrf: \"\",\r\n      },\r\n      code: [],\r\n    };\r\n  },\r\n  methods: {\r\n    sendAlert(type, msg) {\r\n      type = type == 0 ? \"danger\" : \"success\";\r\n      this.code.push({\r\n        id: new Date().getTime(),\r\n        class: \"badge badge-\" + type,\r\n        message: msg,\r\n      });\r\n      setTimeout(() => {\r\n        this.code.splice(0, this.code.length);\r\n      }, 3000);\r\n    },\r\n    send() {\r\n      let canSend = true;\r\n      let description = this.formInfo.server.description;\r\n      let invite = this.formInfo.server.invite;\r\n\r\n      if (description == \"\" || invite == \"\") {\r\n        this.sendAlert(0, \"Preencha todos os campos do formuário\");\r\n        canSend = false;\r\n      }\r\n\r\n      if (description.length > 140) {\r\n        this.sendAlert(0, \"Sua descrição é muito grande\");\r\n        canSend = false;\r\n      }\r\n\r\n      if (canSend === true) {\r\n        let data = new FormData();\r\n        data.append(\"invite\", invite);\r\n        data.append(\"description\", description);\r\n        data.append(\"csrf\", this.formInfo.csrf);\r\n        fetch(this.formInfo.rote, {\r\n          method: \"POST\",\r\n          body: data,\r\n        }).then((resp) => {\r\n          resp.text().then((code) => {\r\n            switch (code) {\r\n              case \"0\":\r\n                this.sendAlert(0, \"Erro ao salvar, tente novamente mais tarde\");\r\n                break;\r\n              case \"1\":\r\n                this.sendAlert(1, \"Edição efeituada com sucesso!\");\r\n                break;\r\n              case \"2\":\r\n                this.sendAlert(0, \"Preencha todos os campos do formuário\");\r\n                break;\r\n              case \"3\":\r\n                this.sendAlert(0, \"Sua descrição é muito grande\");\r\n                break;\r\n              case \"23\":\r\n                this.sendAlert(0, \"Preencha todos os campos do formuário\");\r\n                this.sendAlert(0, \"Sua descrição é muito grande\");\r\n                break;\r\n              default:\r\n                this.sendAlert(0, code);\r\n                break;\r\n            }\r\n          });\r\n        });\r\n      }\r\n    },\r\n  },\r\n  computed: {\r\n    counter: function () {\r\n      return 140 - this.formInfo.server.description.length;\r\n    },\r\n    descTooLong: function () {\r\n      if (this.counter < 0) {\r\n        return true;\r\n      }\r\n      return false;\r\n    },\r\n  },\r\n  created() {\r\n    if (this.content) {\r\n      this.formInfo = JSON.parse(this.content);\r\n    }\r\n  },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.server-title {\r\n  display: flex;\r\n  margin: 2%;\r\n  align-items: center;\r\n}\r\n.server-title h1 {\r\n  margin-left: 1%;\r\n}\r\n\r\n.form-area {\r\n  display: flex;\r\n  height: 75vh;\r\n  align-items: center;\r\n}\r\n.messages-block {\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 40%;\r\n  margin: auto;\r\n  text-align: center;\r\n  font-size: 1.4rem;\r\n}\r\n.message {\r\n  margin: 2px;\r\n  padding: 3%;\r\n}\r\n.bounce-enter-active {\r\n  animation: bounce-in 0.5s;\r\n}\r\n.bounce-leave-active {\r\n  animation: bounce-in 0.5s reverse;\r\n}\r\n@keyframes bounce-in {\r\n  0% {\r\n    transform: scale(0);\r\n  }\r\n  50% {\r\n    transform: scale(1.5);\r\n  }\r\n  100% {\r\n    transform: scale(1);\r\n  }\r\n}\r\n</style>"],"sourceRoot":""}]);
